<template>
  <mt-header :title="title" class="boder-header" >
    <mt-button @click="turnTo" icon="back" slot="left"></mt-button>
  </mt-header>
  
</template>
<script>

export default {
  name: 'GoBack',
  props:{
    url:{
      type:String
    },
    title:{
      title:String
    }
  },
  data() {
    return {

    }
  },
  components: {},
  methods: {
    turnTo(){
      // console.log(this.url)
      if (this.url) {
        this.$router.push(this.url)
      }else{
        this.$router.back()
      }
    },

  },
  watch: {
  },
  mounted() {}
}

</script>
<style lang='postcss' scoped> 



</style>



